import Api from '../../api/user';
	
 export default {
        data() {
            return {
				loginForm: {  
				    workNo: "05370",  
				    password: "123456",  
				}, 
            }
        },
        methods: {
            login: function(e) {
				uni.showLoading({
				    title: '登录中',	
				})
				Api.login(e.detail.value)
				.then(res => {
					uni.hideLoading()
					if(res.code==200){
						uni.showToast({
							icon:'success',
							mask:true,
							title: '登录成功',
							duration: 2000
						})
						uni.hideToast()
						uni.setStorageSync('token', res.data.token);
						if(res.data.role=="0"){
							uni.switchTab({
								url: '/pages/info/info'
							});
						}else if(res.data.role=="1"){
							uni.showToast({
								icon:'success',
								mask:true,
								title: '工程师',
								duration: 2000
							})
							uni.redirectTo({
								url: '/pages/engineer/engineer'
							})
						}else{
							uni.showToast({
								icon:'success',
								mask:true,
								title: '管理员',
								duration: 2000
							})
						}
					}
				})
            }
        }
    }
